<template>
    <div class="jumbotron mt-4">
        <div>
            <h4 class="display-4">Trade or view your portfolio</h4>
            <p class="lead">You may save or load your data</p>
            <p class="lead">Click on "End Day" to begin a new day!</p>

            <hr class="mt-2">

            <strong>Your Funds: {{balanceOutput | currencyFilter}}</strong>
            <p class="mt-2">Stocks Bought: {{portfolioItems.length}}</p>
        </div>
    </div>
</template>

<script>
export default {
   computed: {
       balanceOutput (){
           return this.$store.state.balance;
       },
       portfolioItems(){
           return this.$store.state.portfolioItems
       }
   }
}
</script>