<template>
    <div class="card">
        <div class="card-header bg-success text-white">
            <strong class="stock-name card-title">{{stockName}}</strong> <span>(Price: ${{price}})</span>
        </div>
        <div class="card-body">
            <form class="spaceBetween" @submit="subForm">
                <input type="number" class="form-control" :class="{error:!isPurchasable}" min=0 @input="purchaseCheck" v-model="insertedQuantity"  placeholder="Quantity"/>       <!--makes sure quantity is a number that's greater than 0-->
                <button class="btn btn-success" @click="buyStock" :disabled="insertedQuantity <= 0 && !Number.isInteger(insertedQuantity) || !isPurchasable">Buy</button>
            </form>
            <div v-if="!isPurchasable" class="alert alert-danger p-2 mt-4">
                <p> Unable to purchase due to insufficient funds.</p>
            </div>
        </div>
    </div>
    
</template>

<script>
export default {
    data: function(){
        return{
            insertedQuantity: "",
            isPurchasable: true
        }
    },
    computed:{
        mainBalance(){
            return this.$store.state.balance;
        }
    },
    props:{
        stockName: [String],
        price: [Number]
    },
    methods:{
        purchaseCheck(){
            console.log(this.isPurchasable, this.mainBalance - this.insertedQuantity * this.price > 0);
            if(this.mainBalance - this.insertedQuantity * this.price > 0){
                this.isPurchasable = true;
            }else{
                this.isPurchasable = false;
            }
            
        },
        buyStock(){
            
            if(this.mainBalance > 0 && this.isPurchasable){ //makes sure entered value doesn't exceed the main balance
                const multiply = this.insertedQuantity * this.price;
                this.$store.dispatch("updateBalance", multiply); //update main balance
                                                                    //name must be converted to lowercase to match store data
                this.$store.dispatch("updateQuantity", {name: this.stockName.toLowerCase(), quantity:this.insertedQuantity});
                this.$store.dispatch("updatePortfolioItems", this.stockName.toLowerCase());
                                                            //sending info along with the action dispatch as an object in a parameter
                this.insertedQuantity = "";
                console.log( this.mainBalance - this.insertedQuantity * this.price, multiply, this.$store.state.stockPrices);
            }
           
        },
        subForm(event){
            event.preventDefault();
        }
    }
};
</script>

<style scoped>
    .spaceBetween{
        display:flex;
        flex-direction:row;
        flex-wrap:nowrap;
        justify-content:space-between;
    }
    .card{
        padding: 0 !important;
    }
    .form-control{
        width:30% !important;
    }
    .stock-name{
        font-size:17px;
    }
    .error{
        border: 1px solid rgb(231, 51, 51);
    }
    @media only screen and (max-width:400px){
        .stock-name{
            font-size: 16.5px;
        }
        .card{
            width:100%;
        }
        .card-header{
            padding: .55rem 0.90rem !important;
        }
        .form-control{
        width:45% !important;
        }
    }
    .btn-success.disabled{
        cursor:not-allowed !important;
    }
</style>