<template>
    <header class="mt-4">
      <nav class="navbar navbar-expand-md navbar-light bg-light">
            <router-link class="navbar-brand" to="/" exact tag="a" active-class="active">Stock Trader</router-link>
            <button class="navbar-toggler" data-toggle="collapse" data-target="#mainNav">
                <div>
                    <div> </div>
                    <div> </div>
                    <div> </div>
                </div>
            </button>
        <div class="collapse navbar-collapse" id="mainNav"> 
                <ul class="navbar-nav mr-auto">
                <router-link to="/portfolio" exact class="nav-item" tag="li" active-class="active"> <a class="nav-link ">Portfolio</a> </router-link>
                <router-link to="/stocks" exact class="nav-item" tag="li" active-class="active"><a class="nav-link ">Stocks</a></router-link>
                </ul>
                <ul class="navbar-nav ml-auto">
                <li class="nav-item"> <a class="nav-link" @click="endDay"><i class="fas fa-undo text-dark mr-1"></i>End Day</a></li>
                <li class="nav-item dropdown">
                    <button class="dropdown-toggle btn"  data-toggle="dropdown" type="button"><i class="fas fa-save mr-1"></i>Save & load</button>
                    <div class="dropdown-menu">
                        <a class="dropdown-item" @click="save">Save Data</a>
                        <a class="dropdown-item" @click="load">Load Data</a>
                    </div>
                </li>
                <li class="nav-item"> <a class="nav-link" style="cursor:auto"><strong class="text-dark"><i class="fas fa-dollar-sign mr-1"></i>Funds: {{balanceOutput | currencyFilter}}</strong></a></li>
                </ul>
        
            
                <div v-if="dataSaved" class="fixed-top m-0 alert alert-success text-center py-2">
                    <h6>Data saved!</h6>
                </div>
        </div>
    </nav>
    </header>
</template>

<script>
export default {
    data: function (){
        return{
            dataSaved: false
        }
    },
    computed:{
        balanceOutput (){
            return this.$store.state.balance;
        }
    },
    methods: {
        endDay(){
            console.log(this.stockPrices)
            return this.$store.dispatch("refreshPrices");
        },
        save(){
            const allInfo = {
                balance: this.$store.getters.balance,
                stockPrices: this.$store.getters.stockPrices,
                stocksBought: this.$store.getters.portfolioItems
            };
            this.$http.put("https://mahmoudvue.firebaseio.com/stocks.json", allInfo)
                .then(response =>{
                   console.log(response);
                   this.dataSaved =true;
                  const timeOut =  setTimeout(()=>{
                       this.dataSaved = false;
                       clearTimeout(timeOut);
                   },3000);
                }).catch(err=>{
                    this.dataSaved =false;
                    alert("An error occurred while saving data. Please try again later! " + "["+ err.statusText +"]");
                    console.log(err);
                })                
        },
        load(){
            this.$http.get("https://mahmoudvue.firebaseio.com/stocks.json")
                .then(res =>{
                    if(res && res.body !== null || res.body !== undefined){
                        this.$store.dispatch("loadStocksFromServer", {receivedBalance:res.body.balance, receivedStockPrices: res.body.stockPrices, stocksBought: res.body.stocksBought}) 
                        console.log(res);
                    }
                    
                }).catch(error=>{
                    alert("An error occurred while loading: " + "["+ error.statusText +"]")
                    console.log(error);
                });
            console.log("load");
        }
    }
}
</script>

<style scoped>
    nav{
        border-radius: 6px;
        border: 1px solid #eee;
    }
    a{
        cursor:pointer;
    }
    .active{
        height:100%;
        background-color: #eee;
        padding: 0 2px;
    }
    nav button div > div{
        width:20px;
        height:3px;
        background-color: rgb(22, 26, 36);
        margin:2px 0;
    }
    /* nav button div div::before{
        content: "";
        margin-bottom:4px;
        width:10px;
        height:2.5px;
        background-color: rgb(22, 26, 36);
    }
     nav button div div::after{
        content: "";
        margin-top:4px;
        width:10px;
        height:2.5px;
        background-color: rgb(22, 26, 36);
    } */
</style>