<template>
    <div>     
               <div class="card">
                  <div class="card-header bg-info text-white">
                      <strong>{{type}}</strong> <span>(Price: {{price}} | Quantity: {{quantity}})</span>

                  </div>
                  <div class="card-body">
                      <form class="form" @submit="subForm">
                          <input class="form-control" :class="{error:!isItSellable}" type="number" @input="checkCase" min=0 v-model="insertedValue" placeholder="Quantity"/>
                          <button class="btn btn-info" :disabled="insertedValue.length <=0  && !Number.isInteger(insertedValue) || !isItSellable" @click="sell">Sell</button>
                      </form>
                      <!--Message-->
                    <div v-if="!isItSellable" class="alert alert-danger p-2 mt-4">
                        <p>Not enough quantities to sell.</p>
                    </div> 
                  </div> 
               </div>

    </div>
</template>

<script>
export default {
    data: function(){
        return{
           insertedValue: "",
           isItSellable: true
        }
    },
    props:{
        price:[Number],
        quantity: [Number],
        type : [String],
        entireStock: [Array],
        index: [Number]
    },
    methods:{
        subForm(event){
            event.preventDefault();
        },
        checkCase(){
            if(this.quantity <= 0){ //deletes stocks that have quantites equals or less than 0
                this.entireStock.splice(this.index, 1); //passes the index from the parent element when mapping throught the main array
            }
                    //if the inserted value is greater than the existing quantity, the selling process will be haulted
            if(this.insertedValue > this.quantity){
                 this.isItSellable =false;
            }else{
                this.isItSellable = true;
            }
        },
        sell(){
            console.log(this.index);
             if(this.quantity <= 0){ //deletes stocks that have quantites equals or less than 0
                this.entireStock.splice(this.index, 1); //passes the index from the parent element when mapping throught the main array
                }
            if(this.quantity > 0 && this.insertedValue <= this.quantity ){
               
                const multiply = this.insertedValue * this.price;
                this.$store.dispatch("updateBalanceAfterSelling", multiply);
                this.$store.dispatch("updateQuantityAfterSelling", {type: this.type.toLowerCase(), quantity:this.insertedValue});
                this.insertedValue = "";
                this.isItSellable =true;
            }
        }
    },
    created(){
        if(this.quantity <= 0){ //deletes stocks that have quantites equals or less than 0
                this.entireStock.splice(this.index, 1); //passes the index from the parent element when mapping throught the main array
        }
    }
};
</script>

<style scoped>
    .card-header{
        background-color:#45aebe!important
    }
    .form{
        display:flex;
        flex-direction: row;
        justify-content:space-between;
        flex-wrap:nowrap;
    }
    .form-control{
        width:30% !important;
    }
    .error{
        border: 1px solid rgb(231, 51, 51);
    }
   @media only screen and (max-width:600px){
        .card{
            width:100%;
        }
        .card-header{
            padding: .55rem 0.90rem !important;
        }
        .form-control{
        width:45% !important;
        }
    }
</style>