import Vue from "vue";
import Vuex from "vuex";
import {stockPrices, balance} from "./Data/Data"; //stock & balance data
Vue.use(Vuex); //enable vuex in our application



export const store = new Vuex.Store({
    state:{
        balance,
        stockPrices,
        portfolioItems: []
    },
    getters:{
        balance: state =>{
            return state.balance;
        },
        stockPrices: state=>{
            return state.stockPrices;
        },
        portfolioItems: state =>{
            return state.portfolioItems;
        }
    },
    mutations:{
        updateBalance: (state, payload) =>{
            return state.balance = state.balance -= payload //overwrites the balance with the updated one
        },
        refreshPrices: state=>{
            
           for(let key in state.stockPrices){
            //    console.log(state.stockPrices[key].price);
               state.stockPrices[key].price = Math.round(state.stockPrices[key].price * (1+ Math.random() -0.5 ));
           }

           //------------(Another way - semi-hard coded) JUST FOR REFERENCE------
           // const generateRandom = (max, min)=>{ //randomize prices everyday
            //     return Math.max(Math.floor(Math.random() * max) +1, min)
            // };
            //updates main prices                                   //only change prices
            // state.stockPrices ={  //keeps other data as they are        //max  //min      
            //     bmw: {...state.stockPrices.bmw, price: generateRandom(150, 40)},
            //     google: {...state.stockPrices.google, price: generateRandom(350,40)},
            //     apple: {...state.stockPrices.apple, price: generateRandom(250, 20)},
            //     twitter: {...state.stockPrices.twitter, price: generateRandom(20, 5)},
            //     mercedes: {...state.stockPrices.mercedes, price: generateRandom(180, 70)},
            //     amazon: {...state.stockPrices.amazon, price: generateRandom(280, 90)}

            // }
            //-----------------------------------------------------------------
            //updates prices in "portfolioItems" component
            for( let key in state.portfolioItems){
                const extractProperty = state.portfolioItems[key].type.toLowerCase();
                state.portfolioItems[key].price = state.stockPrices[extractProperty].price;
                console.log(extractProperty);
            }
        },
        updateQuantity: (state, payload) =>{
            console.log(payload);   //changes quantity that belongs to the stock name(both received when dispatching this action)
            return state.stockPrices[payload.name].quantity = payload.quantity;
        },
        updatePortfolioItems: (state, payload) =>{
            const stock = state.stockPrices[payload];
                // const test = {
                //         ...state.stockPrices[payload]
                // }
                 state.portfolioItems.push(stock); //pushes the new stock to the pack
                // console.log(state.portfolioItems[key]);
                // for(let key in state.portfolioItems){
                    // const record = state.portfolioItems.find(el => el.id === state.stockPrices[payload].id);
                        
                    
                    // console.log(record);
                    // if(state.portfolioItems[key].id === state.portfolioItems[payload].id){
                    //     console.log("yes" );
                    // // state.portfolioItems.splice(key,1);
                    // }else{
                    //     console.log("no");
                    // }
                // }
                
        },
        updateBalanceAfterSelling:  (state, payload)=>{
            return state.balance += payload
        },
        updateQuantityAfterSelling: (state, payload)=>{
            console.log(state.stockPrices);
            state.stockPrices[payload.type].quantity -= payload.quantity; //update quantities
            for(let key in state.portfolioItems){ //makes sure quantities gets refreshed
                const extractProperty = state.portfolioItems[key].type.toLowerCase();
                state.portfolioItems[key].quantity = state.stockPrices[extractProperty].quantity;
                console.log(extractProperty);
            }
        },
        loadStocksFromServer:(state, payload)=>{
            console.log(payload.receivedBalance,payload.receivedStockPrices);
            state.balance = payload.receivedBalance ? payload.receivedBalance : 0;
            state.stockPrices =  payload.receivedStockPrices ? payload.receivedStockPrices : {};
            state.portfolioItems = payload.stocksBought ? payload.stocksBought : [];
        }
    },
    actions:{
        updateBalance: ({commit}, payload)=>{
            commit("updateBalance", payload);
        },
        refreshPrices: ({commit})=>{
            commit("refreshPrices");
        },
        updateQuantity: ({commit},  payload)=>{
            commit("updateQuantity", payload);
        },
        updatePortfolioItems: ({commit},payload)=>{
            commit("updatePortfolioItems", payload);
        },
        updateBalanceAfterSelling: ({commit}, payload)=>{
            commit("updateBalanceAfterSelling", payload);
        },
        updateQuantityAfterSelling: ({commit}, payload)=>{
            commit("updateQuantityAfterSelling", payload);
        },
        loadStocksFromServer: ({commit}, payload)=>{
            commit("loadStocksFromServer", payload);
        }
    }
});